                img(src='@/assets/curso/temas/imagen1-slider1-tema2.png', alt='Texto que describa la imagen').imagenes-slider1-tema2
                //figcaption Leyenda de la imagen

            .col-md-6.mb-4.mb-md-0
              h4.texto-slider1-tema2 Suelo: ser vivo
              p.texto-slider1-tema2 El componente del suelo se debe comprender como un ser vivo compuesto por materia orgánica, organismos vivos y minerales como el nitrógeno, el potasio, el fósforo, etc., y demás elementos que las plantas requieren para su adecuado desarrollo vegetativo, floración y fructificación. El suelo posee diferentes requerimientos que le permite reaccionar a las diferentes plantaciones que se realicen allí y, por ende, necesita de una gran atención durante todas las etapas del cultivo, siendo necesario suplir y acondicionar su estado físico, químico y microbiológico según los requerimientos.



          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6
              figure
                img(src='@/assets/curso/temas/imagen2-slider1-tema2.png', alt='Texto que describa la imagen').imagenes-slider1-tema2
                //figcaption Leyenda de la imagen

            .col-md-6.mb-4.mb-md-0
              h4.texto-slider1-tema2 Degradación del suelo
              p.texto-slider1-tema2 En muchas ocasiones, los suelos son deficientes por factores como la degradación, es decir, debido a prácticas agrícolas inadecuadas, por lo que es necesario llevar a cabo procesos de recuperación e incorporación de nutrimentos al suelo mediante labores de fertilización. Sin embargo, como lo indica Arzola et al., (2018), se debe hacer preliminarmente un diagnóstico que incluya todos los componentes del agroecosistema, incluso los de carácter ecológico y social, porque así, se garantizan mejores resultados económicos, agroecológicos y culturales.


      
          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6
              figure
                img(src='@/assets/curso/temas/imagen3-slider1-tema2.png', alt='Texto que describa la imagen').imagenes-slider1-tema2
                //figcaption Leyenda de la imagen

            .col-md-6.mb-4.mb-md-0
              h4.texto-slider1-tema2 Fertilizantes orgánicos
              p.texto-slider1-tema2 La fertilización se hace a través de fertilizantes orgánicos e inorgánicos, elaborados de forma natural o sintética. No obstante, se debe recordar que el uso de agroquímicos trae consecuencias negativas, perjudicando organismos benéficos relacionados con los cultivos, generando resistencia a plagas y enfermedades, contaminando las aguas, e incrementando costos de producción.

          
          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6
              figure
                img(src='@/assets/curso/temas/imagen4-slider1-tema2.png', alt='Texto que describa la imagen').imagenes-slider1-tema2
                //figcaption Leyenda de la imagen

            .col-md-6.mb-4.mb-md-0
              h4.texto-slider1-tema2 Recursos naturales
              p.texto-slider1-tema2 Además, con el aumento día a día de la búsqueda de la sostenibilidad y la eficiencia en el uso de los recursos naturales, se crea una conciencia ambiental que permite garantizar y fomentar una armonía entre la conservación del medio ambiente, el desarrollo económico y el bienestar social.
        

          
          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6
              figure
                img(src='@/assets/curso/temas/imagen5-slider1-tema2.png', alt='Texto que describa la imagen').imagenes-slider1-tema2
                //figcaption Leyenda de la imagen

            .col-md-6.mb-4.mb-md-0
              h4.texto-slider1-tema2 Fertilización
              p.texto-slider1-tema2 El empleo de procesos de fertilización más amigables con el medio ambiente, ha generado que este recurso sea más productivo con el paso de los años, aportando no solo beneficios al suelo, también a las plantas les brinda características y atributos deseables.
 


          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6
