                //figcaption Leyenda de la imagen

            .col-md-6.mb-4.mb-md-0
              h4.texto-slider1-tema2 Elementos minerales
              p.texto-slider1-tema2 Por medio del suministro de elementos minerales que favorecen las propiedades físicas, biológicas y químicas, se logra un manejo sostenible que se manifiesta en una alta productividad y rendimiento. Así mismo, se alcanza sanidad, calidad y un alto valor nutricional en los productos cosechados, además de resistencia a ataques de plagas y enfermedades.


