          p.mb-5(data-aos='fade-right') Las ventajas de algunos tipos de abonos orgánicos son: 


      .bg--img_03.bloque-acordeon-tema2
        .row.justify-content-center.mb-4
          .col-lg-5
            figure(data-aos="zoom-in")
              img(src="@/assets/curso/temas/imagen-izq-acordeon-tema2.png", alt="alt").img-der-acordeon-tema2
          .col-lg-7
            AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjetas-acordeon-tema2")
              .row(titulo="Bocashi")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Materia orgánica fermentada obtenida en menor tiempo que el compost, esto se logra mediante la aceleración de la degradación y el aumento de la temperatura que elimina patógenos. No genera olores desagradables, ni atracción de insectos. A su vez, brinda al suelo nutrientes obtenidos de la fermentación de materiales que contienen elementos mayores y menores, con los cuales se logra un abono superior a los fertilizantes químicos, además, engorda el suelo y provee microorganismos, ofreciendo a las plantas una gran disposición de minerales necesarios para el crecimiento de sus raíces y follaje.

              .row(titulo="Vermicompost – Lombricultura")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Variación del compost rico en nitrógeno, elaborado con heces fecales de lombrices, tierra negra y compost corriente. 

              .row(titulo="Caseros")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Elaborado a partir de estiércol, residuos vegetales, cáscaras de huevo y de plátano.

              .row(titulo="Abonos verdes")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Plantas (leguminosas y gramíneas) sembradas para posteriormente ser incorporadas en terrenos para su descomposición allí, logrando así, la producción de grandes cantidades de biomasa que aportan a la estructura del suelo.

              .row(titulo="Turba")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Materia orgánica fosilizada procedente de minerales y carbohidratos, contiene niveles altos de carbono.

              
              .row(titulo="Cenizas")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Proveniente de madera u hojas que han sido incineradas, son ricas en fósforo y potasio.


              .row(titulo="CAL agrícola")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Su función principal es regular la acidez del abono que se presenta durante el proceso de la fermentación, aumentando las condiciones ideales para el correcto desarrollo de la actividad y reproducción microbiológica durante todo el proceso de fermentación.

              
              .row(titulo="Biofertilizantes")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Preparados que cuentan con células vivas o latentes, provenientes de cepas microbianas que son potenciadoras de diferentes nutrientes, favoreciendo así, la fijación del nitrógeno y la solubilización del fósforo.

              
              .row(titulo="Gallinaza o estiércoles")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Son fuente de nitrógeno que mejora las características vitales y fértiles de la tierra con ciertos nutrientes, principalmente con fósforo, potasio, calcio, magnesio, hierro, manganeso, zinc, cobre y boro, los cuales mejorarán las condiciones biológicas, químicas y físicas del terreno donde se aplicarán los abonos.
              
              
              .row(titulo="Cascarilla de arroz")
                .col-12
                  p.textos-acordeon-tema2.mb-0 Mejora las características físicas de la tierra y de los abonos, facilita la aireación, la absorción de humedad y el filtrado de nutrientes. De igual forma, es una fuente rica en silicio que favorece a los vegetales y los hace más resistentes a ataques de insectos y a enfermedades.
